import {library} from '@fortawesome/fontawesome-svg-core'
import {faSearch} from '@fortawesome/free-solid-svg-icons'
import {faMicroscope} from '@fortawesome/free-solid-svg-icons'
import {faGraduationCap} from '@fortawesome/free-solid-svg-icons'
import {faSquareRootAlt} from '@fortawesome/free-solid-svg-icons'
import {faStethoscope} from '@fortawesome/free-solid-svg-icons'
import {faLaptop} from '@fortawesome/free-solid-svg-icons'

import {required, minLength, sameAs, email} from "vuelidate/lib/validators";
// import {required, numeric, minLength, maxLength} from "vuelidate/lib/validators";


library.add(faMicroscope)
library.add(faSquareRootAlt)
library.add(faStethoscope)
library.add(faGraduationCap)
library.add(faLaptop)
library.add(faSearch)

export default {
  name: 'Home',
  head() {
    return {
      title: 'Online one-to-one tutors',
      meta: [
        {
          name: 'description',
          content: ''
        },
        {
          name: 'keywords',
          content: ''
        }
      ]
    }
  },
  data() {
    return {
      subName: "",
      subdomain: true,
      host: "",
      lessonLoading: false,
      universities_options: [],
      options: [],
      selectedUniversity: null,
      selectedSubject: null,
    }
  },

  mounted() {
    this.loadUniversity()
    // this.Init()
  },
  methods: {
    filterBy: (option, label, search) => {
        return (label || '').toLocaleLowerCase().startsWith(search.toLocaleLowerCase())
    },
    loadUniversity() {
      this.$axios.post('/definitions/universityList',).then((res) => {
        var rows = res.data
        this.universities_options = []
        for (let i = 0; i < rows.length; i++) {
          var row = rows[i]
          this.universities_options.push({id: row.id, text: row.name})
        }
        if(rows.length === 1)
        {
          this.selectedUniversity = this.universities_options[0]
          this.loadSubjects();
        }
      }).catch(error => {
        console.log(error)
      })
    },
    loadSubjects() {
      this.lessonLoading = true
      let data = {};
      if (this.selectedUniversity != null && this.selectedUniversity.id > 0)
        data["uniId"] = this.selectedUniversity.id
      else{
        this.options=[]
        this.selectedSubject = null
        this.lessonLoading = false
        return
      }
      this.$axios.post('/definitions/getAllLessons', data).then((res) => {
        this.options = res.data;
        this.lessonLoading = false;
      }).catch(error => {
        console.log(error)
      })
    },
    clickSearch() {
      if (this.selectedUniversity && this.selectedSubject)
        this.$router.push("/tutors/1?university=" + this.selectedUniversity.id + "&subject=" + this.selectedSubject.id)
    },
  }
}
